.page-lk {



	.top {
		padding-left: 300px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 82px;
		margin-bottom: 30px;
		h1 {
			font-size: 32px;
			font-weight: 600;
		}
		.btn {
			padding: 13px 36px;
			font-size: 14px;
			font-weight: 400;
		}
		position: relative;
		&::before {
			position: absolute;
			bottom: 0;
			right: 20px;
			width: calc(100% - 320px);
			height: 2px;
			background-color: #C8C8C8;
			content: '';
		}


	}

	.pagination {
		display: flex;
		justify-content: center;
		color: #494F54;
		font-size: 16px;
		font-weight: 500;

		&__page {
			display: flex;
			align-items: center;
			justify-content: center;
			min-width: 35px;
			height: 35px;
			&.active {
				border-radius: 5px;
				background: #ECECEC;
			}
		}

	}

	.body {
		display: flex;



		&__aside {
			margin-top: -13px;
			max-width: 300px;
			width: 100%;
			.aside-menu__nav {
				color: #333;
				font-size: 16px;
				font-weight: 500;
				// outline: 1px solid red;
				.active {
					border-radius: 5px;
					background: #F4F4F4;
					color: #F90;
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 12px;
					&::after {
						width: 7px;
						height: 15px;
						background: url("../img/icon/arr-left-sidebar.svg") no-repeat;
						content: '';
					}
				}
				a {
					max-width: 230px;

					display: inline-flex;
					min-height: 48px;
					width: 100%;
					align-items: center;
					padding: 0 18px;


				}


			}
		}

		&__main {
			margin-right: 20px;
			width: 100%;
			&__list {
				margin-bottom: 50px;
				.item {
					min-height: 76px;
					display: flex;
					align-items: center;
					border-bottom: 1px solid #EAEAEA;

					&__main-box {
						display: flex;
						color: #999;
						font-size: 14px;
						font-weight: 400;
						&__num {
							text-align: center;
							padding: 0 11px;
						}
						&__name {
							color: #000;
							span {
								color: #999;
							}
						}


					}

					&__right {
						display: flex;
						gap: 17px;
						margin-left: auto;
						&__edit {
							width: 20px;
							height: 20px;
							background: url("../img/icon/edit-lk.svg") no-repeat;
						}
						&__del {
							width: 23px;
							height: 23px;
							background: url("../img/icon/del-lk.svg") no-repeat;

						}
					}
				}
			}
		}

	}

	&--company {
		.top {
			margin-bottom: 0;
		}
	}

	&--order {
		.top:before {
			display: none;
		}
	}
}
